:root {
  --font-georgia: "Georgia";
  --font-arial: "Arial";
  --color-light-xl: #ffffff;
  --color-light-l: #F6F6F6;
  --color-light-s: #FAFAFA;
  --color-dark-s: #CDCDCD;
  --color-dark-m: #B2B2B2;
  --color-dark-l: #545454;
  --color-dark-xl: #4C4C4C;
  --color-dark-2xl: #444444;
  --color-dark-3xl: #292929;
  --color-primary: #F1CDB3;
  --color-primary-light: #FDDCC4;
  --start-screen-gradient-background: url(/img/noise_lines_transparent@2X.png),
      radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */,
      #211F20;
  --footer-gradient-background: url(/img/noise_lines_transparent@2X.png),
      radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */,
      #211F20;
  --padding: 40px;
  --padding-top: 80px;
  --padding-bottom: 100px;
  --margin-standart: 25px;
}

.h2 {
  margin-top: 0;
  font-family: var(--font-georgia);
  font-style: normal;
  font-size: 44px;
  line-height: 130%;
}

.h3 {
  margin-top: 0;
  font-family: var(--font-georgia);
  font-style: normal;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h3-mobile {
  margin-top: 0;
  font-family: var(--font-georgia);
  font-style: normal;
  font-size: 25px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h4 {
  margin-top: 0;
  font-family: var(--font-georgia);
  font-style: normal;
  font-size: 20px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h5 {
  margin-top: 0;
  font-family: var(--font-georgia);
  font-style: normal;
  font-size: 15px;
  line-height: 130%;
  letter-spacing: 6%;
}

.h5-modal-window {
  margin-top: 0;
  font-family: var(--font-georgia);
  font-weight: bold;
  font-size: 15px;
  line-height: 130%;
  letter-spacing: 6%;
}

.paragraph-s {
  margin-top: 0;
  font-family: var(--font-arial);
  font-size: 12px;
  line-height: 150%;
}

.paragraph-mobile-menu {
  margin-top: 0;
  font-family: var(--font-arial);
  font-size: 32px;
  line-height: 160%;
}

html {
  min-height: 100vh;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

@font-face {
  font-display: swap;
  font-family: "Arial";
  src: local("ArialMT"), url("../fonts/ArialMT.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Arial";
  src: local("Arial-BoldMT"), url("../fonts/Arial-BoldMT.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Georgia";
  src: local("Georgia"), url("../fonts/Georgia.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Georgia";
  src: local("Georgia"), url("../fonts/Georgia.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 160%;
  font-family: var(--font-arial), "Arial", sans-serif;
  font-weight: 400;
  color: var(--color-dark-xl);
  font-style: normal;
  background: var(--color-light-xl);
}

main {
  flex-grow: 1;
  background-color: var(--white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.body {
  position: relative;
}

.table-responsive {
  overflow: hidden;
  overflow-x: auto;
}

.container {
  padding-left: var(--padding);
  padding-right: var(--padding);
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 45px;
  border-radius: 100px;
  font-family: var(--font-georgia);
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 130%;
  letter-spacing: 0.06em;
  color: var(--color-dark-3xl);
  box-sizing: border-box;
  transition: background-color 0.3s ease-in-out;
}

.button--primary {
  background-color: var(--color-primary);
  border: 2px solid var(--color-primary);
}
.button--primary:hover, .button--primary:focus {
  background: var(--color-primary-light);
  cursor: pointer;
}

.button--light {
  background-color: var(--color-light-xl);
  border: 2px solid var(--color-primary);
}
.button--light:hover, .button--light:focus {
  background: var(--color-primary-light);
  cursor: pointer;
}

.header {
  padding-top: 30px;
  padding-bottom: 30px;
}

.header--main {
  position: absolute;
  top: 30px;
  left: 0;
  width: 100%;
  background-color: transparent;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  grid-gap: 35px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__link {
  position: relative;
  display: flex;
  padding-bottom: 3px;
  font-size: 15px;
  line-height: 160%;
  color: var(--color-dark-s);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.menu__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 0;
  height: 3px;
  background-color: transparent;
}
.menu__link:hover, .menu__link:focus {
  color: var(--color-light-s);
  cursor: pointer;
}

.menu__link--active::after {
  left: 0;
  width: 100%;
  background-color: var(--color-primary);
}

.logo {
  display: flex;
  flex-direction: column;
  text-decoration: none;
}

.logo__name {
  margin-top: 0;
  font-family: var(--font-georgia);
  font-weight: bold;
  font-size: 32px;
  line-height: 110%;
  letter-spacing: 6%;
  margin-bottom: 10px;
}
.header--main .logo__name {
  color: var(--color-primary);
}

.logo__subname {
  margin-top: 0;
  font-family: var(--font-arial);
  font-weight: bold;
  font-size: 13px;
  line-height: 100%;
  letter-spacing: 10%;
}
.header--main .logo__subname {
  color: var(--color-light-xl);
}

.main-top {
  padding-top: 180px;
  background: var(--color-dark-3xl);
  background: var(--start-screen-gradient-background);
}

.main-top__wrapper {
  display: flex;
}

.main-top__content {
  flex-basis: 420px;
  padding-top: 163px;
  margin-right: 42px;
  color: var(--color-light-xl);
}

.main-top__h1 {
  margin-top: 0;
  margin-bottom: 42px;
  width: 310px;
  font-family: var(--font-georgia);
  font-style: normal;
  font-size: 44px;
  line-height: 130%;
}

.main-top__text {
  margin-top: 0;
  margin-bottom: 42px;
}

.about__wrapper {
  display: flex;
  align-items: center;
  grid-gap: 120px;
  padding-top: var(--padding-top);
  padding-bottom: var(--padding-bottom);
  max-width: 910px;
}

.about__h2 {
  margin-bottom: var(--margin-standart);
}

.about__content {
  margin-top: 0;
  font-family: var(--font-arial);
  font-size: 15px;
  line-height: 160%;
}
.about__content p {
  margin-top: 0;
}
.about__content p:not(:last-child) {
  margin-bottom: var(--margin-standart);
}

.about__img-wrapper {
  flex-basis: 300px;
}

.main-friends {
  color: var(--color-dark-l);
  background-color: var(--color-light-l);
}

.main-friends__wrapper {
  display: grid;
  justify-items: center;
  padding-top: var(--padding-top);
  padding-bottom: var(--padding-bottom);
}

.main-friends__h2 {
  margin-bottom: 60px;
  text-align: center;
}

.main-friends__slider {
  margin-bottom: 60px;
}

.help__wrapper {
  padding-top: var(--padding-top);
  padding-bottom: var(--padding-bottom);
}

.help__h2 {
  margin-bottom: 60px;
  text-align: center;
}

.help__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-row-gap: 55px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.help__item {
  display: flex;
  flex-basis: 20%;
  flex-shrink: 0;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 30px;
  text-align: center;
}
.help__item svg {
  display: block;
  width: 60px;
  height: 60px;
}
.help__item p {
  margin-top: 0;
  margin-bottom: 0;
}

.friend-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  color: var(--color-dark-l);
  background-color: var(--color-light-xl);
  border-radius: 9px;
  text-decoration: none;
  transition: box-shadow 0.3s ease-in-out;
}
.friend-card:hover, .friend-card:focus {
  box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
}
.friend-card:hover .button, .friend-card:focus .button {
  background: var(--color-primary-light);
  cursor: pointer;
}

.friend__img-wrapper {
  position: relative;
  width: 100%;
  padding-top: 100%;
  object-fit: cover;
  overflow: hidden;
}

.friend__img {
  position: absolute;
  top: 50%;
  left: 50%;
  object-fit: cover;
  transform: translate(-50%, -50%);
}

.friend__description {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 30px;
  padding-top: 30px;
  padding-bottom: 30px;
}

.friend__name {
  font-family: var(--font-georgia);
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 0.06em;
  color: var(--color-dark-l);
}

.friend__text {
  display: none;
}

.slider {
  position: relative;
  padding-left: 105px;
  padding-right: 105px;
  max-width: 100%;
  overflow: hidden;
}

.slider__wrapper {
  overflow: hidden;
}

.slider__list {
  display: grid;
  grid-auto-flow: column;
  grid-gap: 90px;
  grid-auto-columns: minmax(270px, 270px);
}

.slider__arrow {
  position: absolute;
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  background-color: transparent;
  border: 2px solid var(--color-primary);
  border-radius: 50%;
  transform: translateY(-50%);
  transition: background-color 0.3s ease-in-out;
}
.slider__arrow svg {
  display: block;
  width: 14px;
  height: 6px;
}
.slider__arrow:hover, .slider__arrow:focus {
  background-color: var(--color-primary);
  cursor: pointer;
}
.slider__arrow:disabled {
  color: var(--color-dark-s);
  border-color: var(--color-dark-s);
}

.slider__arrow-left {
  left: 0;
  transform: scale(-1, 1) translateY(-50%);
}

.slider__arrow-right {
  right: 0;
  transform: translateY(-50%);
}